/* tslint:disable:component-selector-name  component-selector-type */
import { Component, Input } from '@angular/core';
import { ColumnComponent } from './column.component';
import { DetailTemplateDirective } from './detail-template.directive';
import { DetailsService } from './details.service';

/**
 * @hidden
 */
@Component({
    selector: '[kendoGridTableBody]',
    template: `
    <template ngFor
        [ngForOf]="data"
        let-item
        let-rowIndex="index"
        let-odd="odd">
        <tr [class.k-alt]="odd"
            [class.k-master-row]="detailTemplate?.templateRef"
            [kendoGridSelectable]="selectable"
            [index]="skip + rowIndex">
            <td [class.k-hierarchy-cell]="true"
            *ngIf="detailTemplate?.templateRef">
                <a class="k-icon"
                    [ngClass]="detailButtonStyles(rowIndex)"
                    href="#" tabindex="-1" (click)="toggleRow(rowIndex, item)"></a>
            </td>
            <td
                [ngClass]="column.cssClass"
                [ngStyle]="column.style"
                *ngFor="let column of columns; let columnIndex = index">
                <template
                    [templateContext]="{
                        templateRef: column.templateRef,
                        dataItem: item,
                        column: column,
                        columnIndex: columnIndex,
                        rowIndex: rowIndex,
                        $implicit: item
                        }">
                </template>
                <template [ngIf]="!column.templateRef">{{item | valueOf: column.field: column.format}}</template>
            </td>
        </tr>
        <tr *ngIf="detailTemplate?.templateRef && isExpanded(rowIndex)"
            [class.k-detail-row]="true"
            [class.k-alt]="odd">
            <td [class.k-hierarchy-cell]="true"></td>
            <td [class.k-detail-cell]="true"
                [attr.colspan]="columns.length">
                <template
                    [templateContext]="{
                        templateRef: detailTemplate?.templateRef,
                        dataItem: item,
                        rowIndex: rowIndex,
                        $implicit: item
                        }">
                </template>
            </td>
        </tr>
    </template>
    `
})
export class TableBodyComponent {
    @Input() public columns: Array<ColumnComponent> = [];
    @Input() public detailTemplate: DetailTemplateDirective;
    @Input() public data: Array<any>;
    @Input() public skip: number = 0;
    @Input() public selectable: boolean;

    constructor(
        private detailsService: DetailsService
    ) { }

    public toggleRow(index: number, dataItem: any): boolean {
        this.detailsService.toggleRow(this.skip + index, dataItem);
        return false;
    }

    public isExpanded(index: number): boolean {
        return this.detailsService.isExpanded(this.skip + index);
    }

    protected detailButtonStyles(index: number): any {
        const expanded = this.isExpanded(index);
        return { 'k-minus': expanded, 'k-plus': !expanded };
    }
}



